<template>
	<view class="">
		<list-wrap @refresh='refresh'>
			<text slot="l_tit">{{type==10?'我是车主':'寄件信息'}}</text>
			<text slot="r_tit">{{type==10?'我是乘客':'带物信息'}}</text>
			<view slot="l-list">
				<scroll-view :refresher-triggered='refresh_flag_1' :refresher-enabled='true' scroll-y="true" v-if="list_1.length>0"
				 class='scroll_h' @scrolltolower="more(1)" @refresherrefresh="scrollRefresh(1)">
					<needs v-for="item in list_1" :key='item.id' :info='item'>
						<view class="btn btn-style glost" slot="btns" @click="makeCall(item.phone)">联系TA</view>
						<view class="btn btn-style mar-l-30" slot="btns" @click="pinOwner(item.id)">拼车</view>
					</needs>
				</scroll-view>
				<no-data v-else></no-data>
			</view>
			<view slot="r-list">
				<scroll-view :refresher-triggered='refresh_flag_2' :refresher-enabled='true' scroll-y="true" v-if="list_2.length>0"
				 class='scroll_h' @scrolltolower="more(2)" @refresherrefresh="scrollRefresh(2)">
					<needs v-for="item in list_2" :key='item.id' :info='item'>
						<view class="btn btn-style glost" slot="btns" @click="makeCall(item.phone)">联系TA</view>
						<view class="btn btn-style mar-l-30" slot="btns" @click="pinPassenger(item.id)">拼车</view>
					</needs>
				</scroll-view>
				<no-data v-else></no-data>
			</view>
		</list-wrap>
		<pinForm @change='changeShow' v-if='visibleForm' :type='type' :orderId='order_id'></pinForm>
	</view>
</template>

<script>
	import CarpoolApi from '../../apis/carpool.js'
	import listWrap from '../../base/listWrap.vue'
	import noData from '../../base/nodata.vue'
	import needs from '../../base/needs.vue'
	import pinForm from './pinForm.vue'
	export default {
		name: 'list_temp',
		components: {
			listWrap,
			needs,
			noData,
			pinForm
		},
		mounted() {
			this.init(1, [])
			this.init(2, [])
		},
		props: ['type'], //10人，20物
		data() {
			return {
				visibleForm: false,
				order_id: '',
				postData_1: {
					start_id: '',
					end_id: '',
					depart_time: '',
					identity: 1,
					limit: 4,
					page: 1,
					total: 0
				},
				postData_2: {
					start_id: '',
					end_id: '',
					depart_time: '',
					identity: 2,
					limit: 4,
					page: 1,
					total: 0
				},
				list_1: [],
				list_2: [],
				refresh_flag_1: false,
				refresh_flag_2: false,
			}
		},
		methods: {
			// 车主-1 乘客2
			init(type, concatList) {
				let data = type == 1 ? this.postData_1 : this.postData_2
				data.type = this.type
				CarpoolApi.list(data).then(res => {
					let list = concatList.concat(res.list)
					if (type == 1) {
						this.list_1 = list
						this.postData_1.total = res.total_page
					} else {
						this.list_2 = list
						this.postData_2.total = res.total_page
					}
				})
			},

			refresh(identity, start_id, end_id, depart_time) {
				if (identity == 0) {
					this.postData_1 = Object.assign({}, this.postData_1, {
						start_id,
						end_id,
						depart_time,
						page: 1
					})
				} else {
					this.postData_2 = Object.assign({}, this.postData_2, {
						start_id,
						end_id,
						depart_time,
						page: 1
					})
				}
				this.$nextTick(() => {
					this.init(identity + 1, [])
				})
			},
			scrollRefresh(type) {
				let timer = null
				clearTimeout(timer)
				if (type == 1) {
					this.postData_1.page=1
					this.refresh_flag_1 = true
					timer = setTimeout(() => {
						this.refresh_flag_1 = false
					}, 1500)
				} else {
					this.postData_1.page=2
					this.refresh_flag_2 = true
					timer = setTimeout(() => {
						this.refresh_flag_2 = false
					}, 1500)
				}
				this.$nextTick(()=>{
					this.init(type, [])
				})

			},
			more(type) {
				let page = type == 1 ? ++this.postData_1.page : ++this.postData_2.page
				let total = type == 1 ? this.postData_1.total : this.postData_2.total
				let list = type == 1 ? this.list_1 : this.list_2
				if (page <= total) {
					this.$nextTick(() => {
						this.init(type, list)
					})
				} else {
					this.$util.showToast('没有更多了')
				}
			},
			pinOwner(id) {
				if (uni.getStorageSync('phone')) {
					uni.showModal({
						title: '温馨提示',
						content: '请您确认是否要拼车？',
						success: (res) => {
							if (res.confirm) {
								this.pinOwnerHttp(id)
							}
						}
					})
				} else {
					uni.showModal({
						title: '温馨提示',
						content: '抱歉，您还没有绑定手机号，无法接单，是否前往个人中心绑定手机号？',
						success: (res) => {
							if (res.confirm) {
								uni.switchTab({
									url: '/pages/mine/index'
								})
							}
						}
					})
				}

			},
			pinOwnerHttp(id) {
				CarpoolApi.orderTaking({
					id
				}).then(res => {
					this.$util.showToast('拼车成功', 'success')
					this.list_1 = this.list_1.filter(el => el.id != id)
				})
			},
			pinPassenger(id) {
				this.order_id = id
				this.visibleForm = true
			},
			changeShow(flag, data) {
				if (flag == 'SURE') {
					data = Object.assign({}, data, {
						id: this.order_id
					})
					CarpoolApi.carpool(data).then(res => {
						this.visibleForm = false
						this.$util.showToast('拼车成功', 'success')
					})
				} else {
					this.visibleForm = false
				}
			},
			makeCall(phone) {
				this.$util.makePhone(phone)
			}
		}
	}
</script>

<style lang="less" scoped>
	.scroll_h {
		height: calc(100vh - 200rpx);
		// height: 100%;
	}
</style>
